import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import axios from 'axios'

const Main: React.FC = () => {
  const [data, setData] = useState([]);
  const [data1, setData1] = useState([]);
  const [count, setCount] = useState(0);
  const [isChecks, setIsChecks] = useState(new Array(69).fill(false));
  useEffect(() => {
    setIsChecks(isChecks);
    axios.get("https://mdex123.com/record/currentprice/?platform=mdex")
      .then(res => {
        let d = res.data.data;
        let arr: any = [];
        for (let i = 0; i < d.length; i++) {
          var obj = {
            "isCheck": isChecks[i],
            "symbol": d[i].symbol,
            "price": d[i].price
          };
          arr.push(obj);
        }
        setData(arr);
        setData1(arr.filter((item: { isCheck: boolean; }) => item.isCheck === true));
      })
      .catch(e => {
        console.log('error:' + e);
      })

    const id = setInterval(() => {
      setCount(c => c + 1);
    }, 1000);
    return () => clearInterval(id);
  }, [count, isChecks]);

  const getName = (str: string | undefined) => {
    if (str === undefined) {
      return "error";
    }
    return str.replace(/-HUSD/i, "");
  }

  const getPrice = (num: string | undefined) => {
    if (num === undefined) {
      return -1;
    }
    return parseFloat(num).toFixed(4
    );
  }

  return (
    <MainAll>
      <div>本次使用时长 {count} s</div>

      <MainTop>
        <MainThead>
          <tr>
            <th>置顶</th>
            <th>Heco代币</th>
            <th>HUSD兑换价</th>
          </tr>
        </MainThead>
        <Lab>
          {data1.map((item) =>
            <Coin1>
              <Stick>
                已置顶
              </Stick>
              <Name>
                {getName(item['symbol'])}
              </Name>
              <Price>
                {getPrice(item['price'])}
              </Price>
            </Coin1>
          )}
        </Lab>
        <Lab>
          {data.length !== 0 ? (
            data.map((item, index) => (
              <Coin>
                <Check>
                  <input type="checkbox" onChange={() => isChecks[index] = !isChecks[index]} defaultChecked={isChecks[index]} />
                </Check>
                <Name>
                  {getName(item['symbol'])}
                </Name>
                <Price>
                  {getPrice(item['price'])}
                </Price>
              </Coin>
            ))
          ) : (
              <tr>
                Loading...
              </tr>
            )
          }
        </Lab>
      </MainTop>
    </MainAll>
  )
}

const MainAll = styled.div`
  margin-left: 20%;
  width: 100%;
`

const MainTop = styled.table`
  width: 60%;
  font-size: 14px;
  table-layout: fixed;
  border-collapse: collapse;
  border-spacing: 0;
  box-sizing: border-box;
  text-indent: initial;
  border-color: grey;
  :focus{
    outline: -webkit-focus-ring-color auto 1px;
  }
`

const MainThead = styled.thead`
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
  background: #2a2d32;
`

const Lab = styled.tbody`
  display: table-row-group;
  vertical-align: middle;
  border-color: inherit;
`

const Coin = styled.tr`
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
`;
const Coin1 = styled.tr`
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
  color: red;
`
const Check = styled.td`
padding: 5px 15px;
  line-height: 24px;
  border: 1px solid #333333;
  vertical-align: middle;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;}
`

const Name = styled.td`
  padding: 5px 15px;
  line-height: 24px;
  border: 1px solid #333333;
  vertical-align: middle;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;}
`
const Price = styled.td`
  padding: 5px 15px;
    line-height: 24px;
    border: 1px solid #333333;
    vertical-align: middle;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
`
const Stick = styled.td`
padding: 5px 15px;
    line-height: 24px;
    border: 1px solid #333333;
    vertical-align: middle;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
`
// const View = styled.td`
// padding: 5px 15px;
//     line-height: 24px;
//     border: 1px solid #333333;
//     vertical-align: middle;
//     text-align: center;
//     overflow: hidden;
//     white-space: nowrap;
//     text-overflow: ellipsis;
// `

export default Main;